#user-summary-card {
	height: 97.5%;
}
#user-information-card {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
}
#user-summary-card>.card-menubar {
	position: relative;
}
#user-summary-card>.card-content {
	display: flex;
	flex-direction: column;
	gap: 10px;
	min-height: calc(100% - 40px) !important;
	max-height: calc(100% - 40px) !important;
	height: calc(50% - 40px) !important;
	overflow-y: auto !important;
}
.user-sum-cont {
	padding: 0px;
	border: 1px solid #ccc;
	border-radius: 10px;
}
#user-sum-account-info>.card-content, #user-sum-device-info>.card-content {
	background-color: transparent;
}

#job-card-title {
	height: 30px;
	position: relative;
	padding: 0px;
	margin: 0px;
	display: flex;
	align-items: center;
	justify-content: center;
}
#job-card-title>.card-title {
	position: relative;
	left: 0px;
	top: 0px;
}

.user-summary-form {
	display: flex;
	flex-direction: column;
	gap: 20px;
	padding: 15px;
	background-color: #f9f9f9;
	border-radius: 10px;
}
#user-sum-job-info>.user-summary-form,
#user-sum-user-info>.user-summary-form {
	display: grid;
	grid-template-columns: 50% 50%;
}
.user-summary-form>.form-info-item {
	display: grid;
	grid-template-columns: 30% 70%;
	align-items: center;
}
#user-sum-job-info>.user-summary-form>.form-info-item,
#user-sum-user-info>.user-summary-form>.form-info-item {
	grid-template-columns: 42% 58%;
	align-items: center;
	justify-content: center;
	width: 90%;
}
.user-summary-form>.form-info-item>input {
	width: 100%;
	height: 100%;
}
.user-summary-form>.form-info-item>.form-info-title {
	font-size: 17.5px;
}

#user-sum-remove-confirm {
	width: fit-content;
	min-height: fit-content;
	max-height: fit-content;
}
#user-sum-remove-confirm>.card-content>.text {
	font-size: 25px;
}
#user-sum-remove-confirm>.card-content>.action-row {
	padding: 10px;
}

#confirm-delete-button {
	background-color: #b63d3d;
	font-size: larger;
	font-weight: 600;
	color: #fff;
}
#confirm-delete-button:hover {
	background-color: #8f2727;
}
#go-back-button {
	background-color: #4747be;
}
#go-back-button:hover {
	background-color: #2f2f8d;
}