body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#device-log-cont {
	width: 99%;
	display: grid;
	grid-template-columns: 59% 39%;
	gap: 2%;
	height: 100%;
}

.no-padding {
	padding: 0px;
}

/*LISTS*/

/*Text Only Button*/
.Text-Only-Button {
	width: max-content;
	background-color: transparent;
	filter: unset;
	color: #c94a4c;
	cursor: pointer;
	font-weight: 650;
}

.Text-Only-Button:hover {
	background-color: transparent;
	color: #912d2f;
}

/*LISTS*/
.list-cont {
	border: 1px solid #ccc;
	display: flex;
	flex-direction: column;
	padding: 10px;
	border-radius: 10px;
	background-color: #f9f9f9;
}
.list-cont>.form-info-item.button {
	display: grid;
	align-self: center;
	grid-template-columns: 97% 3%;
	width: 90%;
}
.list-cont>.form-info-item.button>.action-button {
	width: 30px;
	height: 30px;
	filter: unset;
	font-weight: 600;
	text-align: center;
	line-height: 30px;
	background-color: transparent;
}
.list-cont>.form-info-item.button>input {
	height: 30px;
	width: 100%;
}
.numbered-list {
	display: flex;
	flex-direction: column;
	padding: 10px;
	align-items: center;
	justify-content: center;
	gap: 10px;
}
.list-item {
	display: grid;
	width: 100%;
	grid-template-columns: 80% 9% 9%;
	gap: 1%;
	text-align: left;
	line-height: 30px;
	font-size: 20px;
}
.list-item>.action-button {
	width: 100%;
	text-align: center;
	font-size: 15px;
}